import React, { useCallback, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import { ExternalLink } from '../../theme'
import Linktip from '../Linktip'


const StyledMenu = styled.div`

`

const MenuFlyout = styled.span`
  min-width: 26.125rem;
  padding: 0.5rem;
  display: flex;
  font-size: 1rem;
  top: 3rem;
  right: 0rem;
  z-index: 100;
  text-align: center;
  @media screen and (max-width: 630px){
    position: fixed;
    top:auto;
    bottom: 0rem;
    left :0rem;
  }
`

const MenuFlyoutOne = styled.span`
  min-width: 6.125rem;
  padding: 0.5rem;
  display: flex;
  font-size: 1rem;
  top: 16rem;
  right: 0rem;
  z-index: 100;
  flex-direction: column;
`

const MenuItem = styled(ExternalLink)`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`

const MenuDiv = styled.div`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`


const MenuItem1 = styled.div`
  flex: 1;
  padding-left: 0.5rem;
  padding-top: 0.2rem;
  font-weight: 900;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`

// const CODE_LINK = 'https://github.com/Uniswap/uniswap-interface'

export default function HeaderLink() {
  const { t } = useTranslation()
  const node = useRef<HTMLDivElement>()
  const [show1, setShow1] = useState<boolean>(false)

  const open1 = useCallback(() => setShow1(true), [setShow1])
  const close1 = useCallback(() => setShow1(false), [setShow1])

  const [show2, setShow2] = useState<boolean>(false)

  const open2 = useCallback(() => setShow2(true), [setShow2])
  const close2 = useCallback(() => setShow2(false), [setShow2])

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledMenu ref={node as any}>
 
        <MenuFlyout>
          <MenuItem id="link" href="https://colaswap.finance/#/swap" target = "">
            {t('swap')}
          </MenuItem>
          <MenuItem id="link" href="https://coming.colaswap.finance/">
           {t('farms')}
          </MenuItem>
          <MenuItem id="link" href="https://coming.colaswap.finance/">
            NFT
          </MenuItem>
          <MenuItem id="link" href="https://coming.colaswap.finance/">  
          {t('lottery')}
          </MenuItem>
          <MenuDiv onClick={open1}  onMouseEnter={open1} onMouseLeave={close1}>
         {t('bridge')}
         <Linktip content = { <MenuFlyoutOne>
           <MenuItem id="link" href="https://cbridge.celer.network/#/transfer">  
          Cbridge
          </MenuItem>
          <MenuItem id="link" href="https://app.multichain.org/#/router">  
          Multichain
          </MenuItem></MenuFlyoutOne>} show={show1}/>
         </MenuDiv>
          <MenuItem1 onClick={open2}  onMouseEnter={open2} onMouseLeave={close2}> 
            ...
            <Linktip content = { <MenuFlyoutOne>
           <MenuItem id="link" href="https://ido.colaswap.finance/" target = "">  
          IDO
          </MenuItem>
          <MenuItem id="link" href="https://coming.colaswap.finance/" target = "">  
          Info
          </MenuItem></MenuFlyoutOne>} show={show2}/>
          </MenuItem1>
        </MenuFlyout>
    </StyledMenu>
  )
}
